/* index header main nav */
#admin_home_header{
	background-color: rgba(0, 0, 0, 0.95);
	color: whitesmoke;
	height: 15vh;
	width: 100%;
	position: sticky;
	top: 0;
	left: 0;
	z-index: 2;
}

#admin_home_header_nav{
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content:space-around;
}

#admin_home_header_nav_img{
	height: 90%;
}

#admin_home_header_nav_h1{
	color: var(--fourth-color);
	padding: 0.5%;
}

#admin_home_header_nav_a{
	text-decoration: none;
	border: solid var(--fourth-color) 1px;
	box-sizing: border-box;
	padding: 1%;
	padding-left: 2%;
	padding-right: 2%;
	color: var(--fourth-color);
	display: flex;
	justify-content: center;
	align-items: center;
}
#admin_home_header_nav_a:hover{
	background-color: var(--fourth-color);
	color: black;
}


/* admin-page-main1 main*/
#admin-page-main1{
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 110vh;
  width: 100%;
  background-color: var(--third-color);
}
#admin-page-main1::-webkit-scrollbar {
  width: 12px;
}
/* scroll bar thumb (the actual scroll) */
#admin-page-main1::-webkit-scrollbar-thumb {
  background: var(--fourth-color); /* scrollbar color */
}
/* scrroll bar track */
#admin-page-main1::-webkit-scrollbar-track {
  background: var(--third-color); /* track color */
}


/* admin main h2*/
#admin-page-main1-h2{
	color: var(--fourth-color);
	height: 8%;
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-left: 1%;

}

/* admin main section 1  */
#admin-page-main1-section1{
	height: 82%;
	width: 100%;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: wrap;
	border-top: solid 2px rgba(0, 0, 0, 0.7);
	  padding-bottom: 5%;
}

.admin-page-main1-section1-article{
	border: solid var(--fourth-color) 2px;
	background-color: rgba(0, 0, 0, 0.6);
	height: 30%;
	width: 25%;
	margin: 2%;
	border-radius: 15px;
	z-index: 1;
}

#admin-page-main1-section1-article-board_members{
	border: none;
	background-color: var(--main-color);
}
.admin-page-main1-section1-article-a:hover{
	transform: scale(1.05);
	background-color: var(--fourth-color);
	color: black;
		border-radius: 15px;
}

.admin-page-main1-section1-article-a{
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
	text-decoration: none;
	font-size: 140%;
	text-decoration: none;
	transition: 0.5s;
	color: var(--fourth-color);
}








