#admin_home_header {
  color: #f5f5f5;
  z-index: 2;
  background-color: #000000f2;
  width: 100%;
  height: 15vh;
  position: sticky;
  top: 0;
  left: 0;
}

#admin_home_header_nav {
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

#admin_home_header_nav_img {
  height: 90%;
}

#admin_home_header_nav_h1 {
  color: var(--fourth-color);
  padding: .5%;
}

#admin_home_header_nav_a {
  border: solid var(--fourth-color) 1px;
  box-sizing: border-box;
  color: var(--fourth-color);
  justify-content: center;
  align-items: center;
  padding: 1% 2%;
  text-decoration: none;
  display: flex;
}

#admin_home_header_nav_a:hover {
  background-color: var(--fourth-color);
  color: #000;
}

#admin-page-main1 {
  box-sizing: border-box;
  background-color: var(--third-color);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 110vh;
  display: flex;
  overflow: hidden scroll;
}

#admin-page-main1::-webkit-scrollbar {
  width: 12px;
}

#admin-page-main1::-webkit-scrollbar-thumb {
  background: var(--fourth-color);
}

#admin-page-main1::-webkit-scrollbar-track {
  background: var(--third-color);
}

#admin-page-main1-h2 {
  color: var(--fourth-color);
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 8%;
  margin-left: 1%;
  display: flex;
}

#admin-page-main1-section1 {
  box-sizing: border-box;
  border-top: 2px solid #000000b3;
  flex-flow: wrap;
  justify-content: center;
  width: 100%;
  height: 82%;
  padding-bottom: 5%;
  display: flex;
}

.admin-page-main1-section1-article {
  border: solid var(--fourth-color) 2px;
  z-index: 1;
  background-color: #0009;
  border-radius: 15px;
  width: 25%;
  height: 30%;
  margin: 2%;
}

#admin-page-main1-section1-article-board_members {
  background-color: var(--main-color);
  border: none;
}

.admin-page-main1-section1-article-a:hover {
  background-color: var(--fourth-color);
  color: #000;
  border-radius: 15px;
  transform: scale(1.05);
}

.admin-page-main1-section1-article-a {
  width: 100%;
  height: 100%;
  color: var(--fourth-color);
  justify-content: center;
  align-items: center;
  font-size: 140%;
  text-decoration: none;
  transition: all .5s;
  display: flex;
}
